$padding: 1rem;

.container,
.container-fluid {
  margin-left: auto;
  margin-right: auto;
  padding-left: $padding;
  padding-right: $padding;
  width: 100%;
}

@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}

@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}

@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}

@media (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}
